<template>
    <div class="login-page">
        <el-card class="el-card">
            <template #header>黑马面经运营后台</template>
            <el-form :model="form" :rules="rulesForm" ref="refform">
                <el-form-item label="用户名：" prop="username">
                    <el-input v-model="form.username" placeholder="请输入用户名" />
                </el-form-item>
                <el-form-item label="密码：" prop="password">
                    <el-input v-model="form.password" type="password" placeholder="请输入密码"/>
                </el-form-item>
                <el-form-item class="tc">
                    <el-button type="primary" @click="login">登录</el-button>
                    <el-button @click="reset">重置</el-button>
                </el-form-item>
            </el-form>
        </el-card>
    </div>
</template>

<script>
import { mapActions } from 'vuex'
export default {
  name: 'login-page',
  data () {
    return {
      form: {
        username: '',
        password: ''
      },
      rulesForm: {
        username: [
          { required: true, message: '请输入用户名', trigger: 'blur' },
          { min: 5, max: 11, message: '长度在 5 到 11 个字符', trigger: ['blur', 'change'] }
        ],
        password: [
          { required: true, message: '请输入密码', trigger: 'blur' },
          { pattern: /^\w{5,11}$/, message: '请输入 5 到 10 位的密码', trigger: ['blur', 'change'] }
        ]
      }
    }
  },
  methods: {
    // 使用user模块的函数
    ...mapActions('user', ['onLogin']),

    async login () {
      // 兜底校验
      // promise对象，校验是否输入内容，有就是true，没有就是false
      await this.$refs.refform.validate(async valid => {
        if (!valid) return false
        try {
          // 登录请求
          // console.log(this.form)
          await this.onLogin(this.form)
          // 登录成功提示
          this.$message.success('登录成功')
          // 转跳
          // console.log(this.$route.query)
          const p = this.$route.query.redirect || '/'
          this.$router.push(`${p}`)
        } catch (err) {
        //   console.log(err)
          this.$message.error(err.response.data.message)
        }
      })
    },
    reset () {
      this.$refs.refform.resetFields()
    }
  }

}
</script>

<style lang="scss" scoped>
.el-card {
    width: 420px;
    margin: 50px auto;
    /*
    给子组件设置样式要使用深度选择器
    深度作用选择器: :v-deep /deep/

    */
    ::v-deep .el-card__header {
        background: rgba(114, 124, 245, 1);
        text-align: center;
        color: white;

    }
    // .el-button{
    //     margin-left: 80px;
    // }
}
.login-page {
    min-height: 100vh;
        background: url(@/assets/login-bg.svg) no-repeat center / cover;
        display: flex;
        align-items: center;
        justify-content: space-around;

        .el-card {
            width: 420px;

            ::v-deep .el-card__header {
                height: 80px;
                background: rgba(114, 124, 245, 1);
                text-align: center;
                line-height: 40px;
                color: #fff;
                font-size: 18px;
            }
}
}
  .tc {
      text-align: center;
  }
   .el-form {
       padding: 0 20px;
   }
</style>
